import * as React from "react";
import { StyleSheet, View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";

export default function BoxChildren({
  styles,
  children
}) {
  return <View style={styles ? styles?.container : defaultStyles.container}>{children}</View>
}
const defaultStyles = StyleSheet.create({
  container: {
    minWidth: 150,
    minHeight: 100,
    borderRadius: 10,
    margin: 5,
    padding: 10,
    backgroundColor: layoutConstants.bgColorSecondary,
  }
});
