import { Provider } from "@react-native-material/core";
import { NavigationContainer } from "@react-navigation/native";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { Amplify } from "aws-amplify";
import * as React from "react";
import { MenuProvider } from "react-native-popup-menu";
import { Provider as ReduxProvider } from "react-redux";
import awsmobile from "./app/aws-configs";
import DailyScreen from "./app/screens/daily-screen";
import MonthScreen from "./app/screens/month-screen";
import NoteDetailScreen from "./app/screens/note-detail-screen";
import { UserSettingsScreen } from "./app/screens/user-settings-screen";
import { ServicesProvider } from "./app/services/providers/services-provider";
import store from "./app/store";
import { layoutConstants } from "./app/themes/layout-contants";

Amplify.configure(awsmobile);

const Stack = createNativeStackNavigator();

function App() {
  const config = {
    screens: {
      Month: "month",
      Daily: "daily",
      NoteDetail: "notedetail",
    },
  };

  const linking = {
    prefixes: ["https://elearning01.github.io"],
    config,
  };

  return (
    <ReduxProvider store={store}>
      <Provider>
        <ServicesProvider>
          <MenuProvider>
            <NavigationContainer linking={linking}>
              <Stack.Navigator>
                <Stack.Screen
                  name="month"
                  component={MonthScreen}
                  options={{
                    headerShown: false,
                  }}
                />
                <Stack.Screen
                  name="daily"
                  component={DailyScreen}
                  options={{
                    headerShown: false,
                  }}
                />
                <Stack.Screen
                  name="notedetail"
                  component={NoteDetailScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    headerTintColor: "white",
                    title: "Note",
                    headerTitleAlign: "center",
                    headerTitleStyle: { color: "white" },
                  }}
                />
                <Stack.Screen
                  name="usersettings"
                  component={UserSettingsScreen}
                  options={{
                    headerStyle: {
                      backgroundColor: layoutConstants.colorPrimary,
                    },
                    headerTintColor: "white",
                    title: "Settings",
                    headerTitleAlign: "center",
                    headerTitleStyle: { color: "white" },
                  }}
                />
              </Stack.Navigator>
            </NavigationContainer>
          </MenuProvider>
        </ServicesProvider>
      </Provider>
    </ReduxProvider>
  );
}

export default App;
