import { Text } from "@react-native-material/core";
import dayjs from "dayjs";
import { StyleSheet, View } from "react-native";
import { layoutConstants } from "../../themes/layout-contants";

export default function InlineCalendar({ styles }) {
  const currentDate = dayjs();
  const stepDays = [-3, -2, -1, 0, 1, 2, 3];
  const dayOfWeek = ["Sun", "Mon", "Tue", "Web", "Thu", "Fri", "Sat"];
  return (
    <View style={{ display: "flex", flexDirection: "row" }}>
      {stepDays.map((d) => {
        const day = currentDate.add(d, 'day');
        return (
          <View key={d} style={{flex: 1, alignItems: "center"}}>
            <Text style={d == 0 ? defaultStyles.active : defaultStyles.inactive}>{dayOfWeek[day.day()]}</Text>
            <Text style={d == 0 ? defaultStyles.active : defaultStyles.inactive}>{day.format("DD")}</Text>
          </View>
        )
      })}
    </View>
  );
}
const defaultStyles = StyleSheet.create({
  container: {
    minWidth: 150,
    minHeight: 100,
    borderRadius: 10,
    margin: 5,
    padding: 10,
    backgroundColor: layoutConstants.bgColorSecondary,
  },
  active: {
    color: layoutConstants.colorPrimary,
    fontWeight: 'bold'
  },
  inactive: {
    color: layoutConstants.colorBlur
  }
});

