import React, { useEffect } from "react";
import { Image, StyleSheet, View } from "react-native";
import MonthCalendar from "../components/month/month-calendar";
import BottomBarCustom from "../components/utils/bottom-bar-custom";
import { componentConstants } from "../components/utils/component-contants";
import { ViewLandscape } from "../components/utils/view-landscape";
import { layoutConstants } from "../themes/layout-contants";
import BoxLabel from "../components/home/box-label";
import { createMonthService } from "../services/month-service";
import { LinearGradient } from "expo-linear-gradient";

const MonthScreen = ({ navigation, styles }) => {
  const [imageUrl, setImageUrl] = React.useState(null);
  const monthService = createMonthService();
  useEffect(() => {
    const getImageSync = async () => {
      const image = await monthService.getImage();
      setImageUrl(image);
    };
    getImageSync();
  }, []);

  return (
    <View style={{ flex: 1 }} testID="month-screen">
      <ViewLandscape noScroll={true}>
        <View style={{ flex: 1 }} testID={"month-screen-body"}>
          <LinearGradient
            colors={["#90A4AE", "#CFD8DC", "white"]}
            style={{ height: "38%", justifyContent: "center" }}
          >
            <BoxLabel
              styles={{
                image: { width: 180, aspectRatio: 1, resizeMode: "contain" },
                box: { justifyContent: "center" },
              }}
              awsUrl={imageUrl}
            ></BoxLabel>
          </LinearGradient>
          <View testID={"month-screen-calendar"} style={{ flex: 1,backgroundColor: "white" }}>
            <MonthCalendar
              onDateClick={() => {
                navigation.navigate("Daily");
              }}
            />
          </View>
        </View>
      </ViewLandscape>
      <BottomBarCustom
        page={componentConstants.MONTH_SCREEN}
        navigation={navigation}
      ></BottomBarCustom>
    </View>
  );
};

const styles = StyleSheet.create({
  daily: {
    height: 100,
    padding: 5,
    color: layoutConstants.textColorSecondary,
  },
  textDaily: {
    fontWeight: layoutConstants.textBold,
    color: layoutConstants.textColor,
    fontSize: layoutConstants.quoteSize,
    fontStyle: "italic",
    paddingHorizontal: 22,
  },
  article: {
    height: 60,
    width: 300,
  },
  textArticle: {
    fontWeight: layoutConstants.textBold,
    color: layoutConstants.textColorThird,
    paddingHorizontal: 10,
  },
});
export default MonthScreen;
