import { log } from "components/utils/log";
import { useDispatch } from "react-redux";
import NoteDao from "../models/note/note-dao";
import { string } from "yup";
import { NoteListValidator, NoteValidator } from "../models/category/note-validator";


export default class NoteService {
  dao;
  dispatch;
  constructor() {
    this.dao = new NoteDao();
    this.dispatch = useDispatch();
  }

  /**
   * 
   * @param {string} date 23-02-17
   * @returns 
   */
  async getNotesByDate(date) {
    log.debug("getNotesByDate", date);
    try {
      await this.dateValidator().validate(date)
      const raw = await this.dao.getNotesByDate(date);
      let rawTasks = JSON.parse(raw);
      return rawTasks;
    } catch (error) {
      log.warn(error);
      return [];
    }
  }
  /**
   * 
   * @param {string} date 23-02-17
   * @param {array} value [{description: "nguyen tan phat", type: "Priority"}]
   * @returns 
   */
  async saveNotesByDate(date, value) {
    log.debug("saveNotesByDate", date);
    try {
      if(!value || !date) {
        return;
      }
      await NoteListValidator.validate(value);
      await this.dateValidator().validate(date)
      await this.dao.setNoteByDate(date, JSON.stringify(value));
    } catch (error) {
      log.warn(error);
      return [];
    }
  }

  /**
   * 
   * @param {string} date 23-02-17
   * @param {array} value {description: "nguyen tan phat", type: "Priority"}
   * @param {number} index 1
   * @returns 
   */
  async saveNoteByDateIndex(date, value, index) {
    log.debug("saveNoteByDateIndex", date);
    try {
      if(!value || !date) {
        return;
      }
      await NoteValidator.validate(value);
      await this.dateValidator().validate(date);
      const raw = await this.dao.getNotesByDate(date);
      let rawTasks = JSON.parse(raw);
      rawTasks[index] = value;
      await NoteListValidator.validate(rawTasks);
      await this.dao.setNoteByDate(date, JSON.stringify(rawTasks));
    } catch (error) {
      log.warn(error);
      return [];
    }
  }


  /**
   * 
   * @param {string} date 23-02-17
   * @returns 
   */
  dateValidator(date) {
    return string().matches(/^[0-9]+-[0-9]+-[0-9]+$/)
  }
}

let service;
const createNoteService = () => {
  if (service) {
    return service;
  }
  service = new NoteService();
  return service;
};

export { createNoteService };

