import { Button, TextInput } from "@react-native-material/core";
import { HeaderBackButton } from "@react-navigation/elements";
import dayjs from "dayjs";
import React, { useEffect } from "react";
import { StyleSheet, View } from "react-native";
import { log } from "utils/log";
import { componentConstants } from "../components/utils/component-contants";
import HideKeyboard from "../components/utils/hide-keyboard";
import { ViewLandscape } from "../components/utils/view-landscape";
import { NoteValidator } from "../models/category/note-validator";
import NoteModel from "../models/note/note-model";
import { createNoteService } from "../services/note-service";
import { layoutConstants } from "../themes/layout-contants";

export default function NoteDetailScreen({ route, navigation }) {
  // on init, title are null
  // on update, title base on data model
  // on action create/update form, category is submited
  const { params } = route;
  const note = params?.note;
  const back = params?.back;
  const index = params?.index;
  const [description, setDescription] = React.useState(note?.description || "");
  const isUpdate = !!note;
  const service = createNoteService();
  log.debug(params);

  useEffect(() => {
    navigation.setOptions({
      headerLeft: (props) => (
        <HeaderBackButton
          {...props}
          onPress={async () => await saveAndBack()}
          label={back?.component || componentConstants.DAILY_SCREEN}
          labelVisible={true}
          tintColor={layoutConstants.textColor}
        />
      ),
    });
  }, []);

  const saveAndBack = async () => {
    await handelSave(note, description, index);
    navigation.navigate(back?.component || componentConstants.DAILY_SCREEN);
  };

  const handelSave = async (note, description, index) => {
    const id = note ? note.id : null;
    let newItem = new NoteModel({
      id,
      description,
      type: note.type
    });
    // validate here
    await NoteValidator.validate(newItem);
    await service.saveNoteByDateIndex(dayjs().format("YY-MM-DD"), newItem, index);
    return newItem;
  };

  return (
    <ViewLandscape>
      <View style={{ flex: 1 }}>
        <View>
          <TextInput
            autoFocus={true}
            variant="standard"
            value={description}
            onChangeText={setDescription}
            style={{ color: layoutConstants.textColor, padding: 10 }}
            placeholder="Note"
          />
        </View>
        <HideKeyboard>
          <View style={{ flex: 1 }}></View>
        </HideKeyboard>
        <View>
          <Button
            disabled={!description}
            title={"Save"}
            onPress={async () => await saveAndBack()}
            color={layoutConstants.colorPrimary}
            tintColor={"white"}
            style={{ width: "100%", marginBottom: 20 }}
          />
        </View>
      </View>
    </ViewLandscape>
  );
}