import DaoBase from "../dao-base";

export default class NoteDao extends DaoBase {
  async getNotesByDate(date) {
    const key = this.getKeyPrefix() + date;
    return await this.getItem(key);
  }

  async setNoteByDate(date, value) {
    const key = this.getKeyPrefix() + date;
    if(!value) {
      return;
    }
    await this.setItem(key, value);
  }

  getKeyPrefix() {
    return "notes-";
  }
}

export const noteDao = new NoteDao();
