import dayjs from "dayjs";
import { log } from "../components/utils/log";
import { layoutConstants } from "../themes/layout-contants";
import { requestUrlS3 } from "./utils";

export default class MonthService {
  async getImage(date = null) {
    let currentDate = dayjs();
    if(date) {
      currentDate = dayjs(date);
    }
    const month = currentDate.month();
    log.debug("getImage", layoutConstants.MONTH_URLS[month]);
    return layoutConstants.MONTH_URLS[month];
  }
}

let service;
const createMonthService = () => {
  if (service) {
    return service;
  }
  service = new MonthService();
  return service;
};
export { createMonthService };

